#progress {
    position: absolute;
    top: 1%;
    left: 1%;
    display: flex;
    width: 100%;
    height: 5%;
}

.help {
    width: 4%;
    height: 103%;
    padding-top: 0.5%;
    background-color: #ccc;
    font-size: 90%;
    border-radius: 50%;
    box-shadow: 2px 2px;
    color: #fff;
    transition: 0.2s;
    cursor: help;
    /* margin: auto; */
}

.help-content{
    display: none;
    margin-left: 2%;
    left: -15px;
    font-size: 100%;
    line-height: 30px;
    z-index: 10;
    background-color: #646569;
    color: #fff;
    text-align: left;
    padding-left: 3%;
    padding-right: 3%;
    border-radius: 10px;
}

.help-content:before{
  content: '';
  position: absolute;
  display: none;
  top: 20%;
  left: 5.2%;
  border-right: 8px solid #646569;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
}


.help:hover {
    background-color: #aaa;
}

.help:hover + .help-content {
    display: block;
}

.help:hover + .help-content:before{
    display: block;
}

/* .help + .help-content {
    display: block;
} */


.eachDiscardContainer
{
    padding-left: 0;
    width: 100px;
    height: 150px;
    position: absolute;
}
.eachDiscardButton
{
    border: 1px solid #000;
    border-radius: 10px;
    padding-left: 0;
    transition-duration: 2.5s;
    transition-property: top;
    width: 100%;
    height: 100%;
}

.eachDiscardImage
{
    margin: 0;
    border-radius: 10px;
    object-fit: cover;
    width: 100%;
    height: 100%;
    /* transition-duration: 20s; */
}

.discard
{
    position: absolute;
    top: 30%;
    left: 2%;
    /* margin: 50%; */
}

.deleteMsg {
    display: inline-block;
    padding: 10px;
    margin: auto;
    border-bottom: 3px solid #999;
    font-weight: 400;
    background-color: rgba(150,150,150,0.5);
    color: #222;
    animation: fadeOut ease 4s;
    -webkit-animation: fadeOut ease 4s linear forwards;
    -moz-animation: fadeOut ease 4s linear forwards;
    -o-animation: fadeOut ease 4s linear forwards;
    -ms-animation: fadeOut ease 4s linear forwards;
    opacity: 0;
}

@-webkit-keyframes fadeOut {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        display: none;
    }
}

@keyframes fadeOut {
